	import {edituserPwd} from '../../api/user/userinfo.js'
	export default{
		data(){
			return{
				formModel:{
					fields:{
						oldpwd:'',
						newpwd:'',
						newpwd2:''
					}
				}
			}
		},
		methods:{
			confirmfrom(){
				let valid = this.validpwd();
				if(valid!="true")
				{
					uni.showModal({		
							title:'提示',
							content:valid,
							showCancel:false
					})
				}
				else{
					uni.showModal({
						title:'提示',
						content:'是否确认修改密码',
						success: (r) => {
							if(r.confirm)
							{
								this.submitform();
							}					
						}
					})
				}
			},
			submitform(){
				edituserPwd(this.formModel.fields).then(res=>{
					console.log(res.data)
					if(res.data.code==200)
					{
						uni.showModal({
							title:'提示',
							content:'修改密码成功',
							success: (r) => {
								if(r.confirm)
								{
									this.$store.commit('logout');
									uni.clearStorage();
									uni.reLaunch({
										url:'../login/login'
									})
								}					
							}
						})
					}
					else
					{
						uni.showModal({		
								title:'提示',
								content:res.data.message,
								showCancel:false
						})
					}
				})
			},
			validpwd(){
				let _valid = "true";
				if(this.formModel.fields.oldpwd==''||this.formModel.fields.oldpwd==null)
				{
					_valid="原密码不能为空";
					return _valid;
				}
				if(this.formModel.fields.newpwd==''||this.formModel.fields.newpwd==null)
				{
					_valid="新密码不能为空";
					return _valid;
				}
				if(this.formModel.fields.newpwd2==''||this.formModel.fields.newpwd2==null)
				{
					_valid="确认密码不能为空";
					return _valid;
				}	
                if(this.formModel.fields.newpwd!=this.formModel.fields.newpwd2)
				{
					_valid="新密码与确认密码不一致";
					return _valid;
				}
				return _valid;
			}
		}
	}
